import merge from "lodash/merge";
import React, { FC } from "react";
import ReactApexChart from "react-apexcharts";
import styled from "styled-components";
import { colors, legacyColors, ThemeColor } from "../../theme/colors";
import BaseOptionChart from "../../theme/charts";
import { fNumber } from "../../../utils/number";

export interface ChartProps {
  color?: ThemeColor;
  title?: string;
  subtitle?: string;
  chartLabels: string[];
  chartData: {
    label: string;
    value: number;
  }[];
  chartColors: string[];
  diameter: string;
}

const PieChart: FC<ChartProps> = ({
  diameter = "100%",
  chartData,
  chartColors,
}) => {
  const chartLabels = chartData.map((i) => i?.label);
  const chartSeries = chartData.map((i) => i?.value);

  const chartOptions = {
    series: [44, 55, 13, 33],
    labels: chartLabels,
    chart: {
      type: "donut",
      length: 3,
    },
    length: 3,
    colors: ["#FFFFF", "#FFFFF", "#FFFFF"],

    stroke: {
      colors: [
        legacyColors.grey500,
        legacyColors.grey500,
        legacyColors.grey500,
      ],
    },
    legend: { floating: true, horizontalAlign: "center" },
    tooltip: {
      fillSeriesColor: false,
      y: {
        formatter: (seriesName: string) => fNumber(seriesName),
        title: {
          formatter: (seriesName: string) => `${seriesName}`,
        },
      },
    },
  };

  const options = {
    series: [44, 55, 13, 33],
    labels: ["Apple", "Mango", "Orange", "Watermelon"],
  };

  return (
    <Container>
      <ReactApexChart type="donut" options={chartOptions} height={diameter} />
    </Container>
  );
};

export default PieChart;

const Container = styled.div`
  color: white;
`;
